<template>
    <div>
        <da-manage-page>
            <div slot="button">
                <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
            </div>
            <el-form slot="search" :inline="true" :model="searchForm.formdata">                
                <el-form-item label="表计类型名称">
                    <el-input v-model="searchForm.formdata.name"></el-input>
                </el-form-item>

                <el-form-item label="生产厂家">
                    <!-- <el-input v-model="searchForm.formdata.manufacturerId"></el-input> -->
                    <el-select v-model="searchForm.formdata.manufacturerId" filterable size="medium" placeholder="请选择">
                        <el-option v-for="(item, index) in manufacturerData" :key="index" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="getTbList">查询</el-button>
                </el-form-item>
            </el-form>
            <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

                <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
                    <template slot-scope="scope">
                        <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
                        <el-button size="small" @click="delTb(scope.row)">删除</el-button>
                    </template>
                </el-table-column>

            </da-table>
        </da-manage-page>

        <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
            <el-form label-width="120px" :model="addForm" :rules="rules" ref="addForm">

                <!-- <el-form-item label="企业名称" prop="entId">
                    <el-select v-model="addForm.entId" filterable size="medium" placeholder="请选择">
                        <el-option v-for="(item, index) in entData" :key="index" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="表计类型名称" prop="name">
                    <el-input auto-complete="off" v-model="addForm.name"></el-input>
                </el-form-item>
                <el-form-item label="量程" prop="maxRange">
                    <el-input auto-complete="off" v-model="addForm.maxRange"></el-input>
                </el-form-item>
                <el-form-item label="型号" prop="model">
                    <el-input auto-complete="off" v-model="addForm.model"></el-input>
                </el-form-item>
                <el-form-item label="规格" prop="specifications">
                    <el-input auto-complete="off" v-model="addForm.specifications"></el-input>
                </el-form-item>
                <el-form-item label="生产厂家" prop="manufacturerId">
                    <!-- <el-input auto-complete="off" v-model="addForm.manufacturerId"></el-input> -->
                    <el-select v-model="addForm.manufacturerId" filterable size="medium" placeholder="请选择">
                        <el-option v-for="(item, index) in manufacturerData" :key="index" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="测量精度" prop="accuracy">
                    <el-input auto-complete="off" v-model="addForm.accuracy"></el-input>
                </el-form-item>
                <el-form-item label="准确度等级" prop="accuracyLevel">
                    <el-input auto-complete="off" v-model="addForm.accuracyLevel"></el-input>
                </el-form-item>
                <el-form-item label="测量范围" prop="measureRange">
                    <el-input auto-complete="off" v-model="addForm.measureRange"></el-input>
                </el-form-item>
                <el-form-item label="计量能源" prop="energyId">
                    <!-- <el-input auto-complete="off" v-model="addForm.energyId"></el-input> -->
                    <el-select v-model="addForm.energyId" filterable size="medium" placeholder="请选择">
                        <el-option v-for="(item, index) in energyData" :key="index" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="通信方式" prop="msgType">
                    <el-input auto-complete="off" v-model="addForm.msgType"></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
            </div>
        </da-dialog>
    </div>
</template>
<script>
export default {
  name: "instrument",
  data() {
    return {
      entData: [],
      manufacturerData: [],
      energyData: [],
      // 查询表单
      searchForm: {
        formdata: { entId: "", name: "", manufacturerId: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头

        // {
        //   label: "企业名称",
        //   prop: "entId",
        //   align: "left",
        //   sortable: true,
        //   displace: {}
        // },
        { label: "表计类型名称", prop: "name", align: "left", sortable: true },
        { label: "量程", prop: "maxRange", align: "left", sortable: true },
        { label: "型号", prop: "model", align: "left", sortable: true },
        {
          label: "规格",
          prop: "specifications",
          align: "left",
          sortable: true
        },
        {
          label: "生产厂家",
          prop: "manufacturerId",
          align: "left",
          sortable: true
        },
        { label: "测量精度", prop: "accuracy", align: "left", sortable: true },
        {
          label: "准确度等级",
          prop: "accuracyLevel",
          align: "left",
          sortable: true
        },
        {
          label: "测量范围",
          prop: "measureRange",
          align: "left",
          sortable: true
        },
        { label: "计量能源", prop: "energyId", align: "left", sortable: true },
        { label: "通信方式", prop: "msgType", align: "left", sortable: true }
      ],
      dialogVisible: false,
      addForm: {
        entId: "",
        name: "",
        maxRange: "",
        model: "",
        specifications: "",
        manufacturerId: "",
        accuracy: "",
        accuracyLevel: "",
        measureRange: "",
        energyId: "",
        msgType: ""
      },
      rules: this.eachValidate({
        entId: { required: true },
        name: { required: true, maxString: "40" },
        maxRange: { required: true },
        model: { maxString: "20" },
        specifications: { maxString: "20" },
        manufacturerId: {},
        accuracy: { maxString: "20" },
        accuracyLevel: { maxString: "20" },
        measureRange: { maxString: "20" },
        energyId: {},
        msgType: { maxString: "50" }
      })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑表计类型管理" : "新增表计类型管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getEnt() {
      this.$ajax.get("rest/enterprise/list").then(res => {
        if (res.data.status) {
          this.entData = res.data.records;
          //   let toObj = {};
          //   this.$each(res.data.records, item => {
          //     toObj[item.id] = item.name;
          //   });
          //   this.colsData[0].displace = toObj;
        }
      });
    },
    getManufacturerInfo() {
      this.$ajax.get("rest/instrument/manufacturer").then(res => {
        if (res.data.status) {
          this.manufacturerData = res.data.records;
          let toObj = {};
          this.$each(res.data.records, item => {
            toObj[item.id] = item.name;
          });
          this.colsData[4].displace = toObj;
        }
      });
    },
    getEnergyInfo() {
      this.$ajax.get("rest/instrument/energy").then(res => {
        if (res.data.status) {
          this.energyData = res.data.records;
          let toObj = {};
          this.$each(res.data.records, item => {
            toObj[item.id] = item.name;
          });
          this.colsData[8].displace = toObj;
        }
      });
    },
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/instrument/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          entId: "",
          name: "",
          maxRange: "",
          model: "",
          specifications: "",
          manufacturerId: "",
          accuracy: "",
          accuracyLevel: "",
          measureRange: "",
          energyId: "",
          msgType: ""
        };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/instrument", this.addForm).then(
            res => {
              if (res.data.status) {
                this.$message.success(res.data.answerMsg);
                this.dialogVisible = false;
                this.getTbList();
              } else {
                this.$message.error(res.data.answerMsg);
              }
            }
          );
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/instrument", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    }
  },
  mounted() {
    this.getEnt();
    this.getTbList();
    this.getEnergyInfo();
    this.getManufacturerInfo();
  }
};
</script>



